import { SheetUtils } from '../../../utils/SheetUtils';
import { CellFont } from './CellFont';
import { CellBorder } from './CellBorder';
import { XIcon } from '../xicon/XIcon';
import XTableFormat from '../XTableTextFormat';
import { Compile, Instruct } from '../../../formula/Compiler';
import { RichFonts } from './RichFonts';

/**
 * 
 */
class Cell {

  /**
   * Cell
   * @param background
   * @param readOnly
   * @param format
   * @param text
   * @param richText
   * @param ruler
   * @param custom
   * @param icons
   * @param borderAttr
   * @param fontAttr
   * @param formula
   * @param contentWidth
   * @param contentHeight
   * @param contentType
   */
  constructor({
    background = SheetUtils.Nul,
    readOnly = false,
    formula = null,
    icons = [],
    format = 'default',
    text = SheetUtils.EMPTY,
    richText = SheetUtils.EMPTY,
    ruler = null,
    custom = {},
    borderAttr = {},
    fontAttr = {},
    contentWidth = 0,
    contentHeight = 0,
    contentType = Cell.TYPE.STRING,
  } = {}) {
    // 
    this.icons = XIcon.newInstances(icons);
    // 
    this.background = background;
    // 
    this.custom = custom;
    // 
    this.ruler = ruler;
    // 
    this.readOnly = readOnly;
    // 
    this.format = format;
    // 
    this.contentType = contentType;
    // 
    this.formula = formula;
    // 
    this.formulaInstruct = null;
    // 
    this.text = text;
    // 
    this.richText = new RichFonts(richText);
    // 
    this.formatText = null;
    // 
    this.formulaText = null;
    // 
    this.contentHeight = contentHeight;
    // 
    this.contentWidth = contentWidth;
    // 
    this.fontAttr = new CellFont(fontAttr);
    // 
    this.borderAttr = new CellBorder(borderAttr);
  }

  /**
   * 
   * @returns {string|*}
   */
  getFormatText() {
    let { contentType, formulaInstruct, format, formula } = this;
    let { text, formulaText, formatText } = this;
    // 
    if (formula) {
      if (!formulaText) {
        if (formulaInstruct) {
          formulaText = Instruct(formulaInstruct);
          this.formulaText = formulaText;
        } else {
          formulaInstruct = Compile(formula);
          this.formulaInstruct = formulaInstruct;
          formulaText = Instruct(formulaInstruct);
        }
      }
      if (format) {
        // 
        if (!formatText) {
          formatText = XTableFormat(format, formulaText);
          this.formatText = formatText;
        }
      }
      return formatText;
    }
    // 
    switch (contentType) {
      case Cell.TYPE.STRING:
      case Cell.TYPE.NUMBER:
      case Cell.TYPE.DATE_TIME: {
        if (format) {
          if (!formatText) {
            this.formatText = XTableFormat(format, text);
            return this.formatText;
          }
        }
        return formatText;
      }
    }
    // 
    return SheetUtils.EMPTY;
  }

  /**
   * 
   * @returns {string|*}
   */
  getComputeText() {
    let { formula, formulaInstruct, contentType } = this;
    let { richText, formulaText, text } = this;
    // 
    if (formula) {
      if (!formulaText) {
        if (formulaInstruct) {
          formulaText = Instruct(formulaInstruct);
          this.formulaText = formulaText;
        } else {
          formulaInstruct = Compile(formula);
          this.formulaInstruct = formulaInstruct;
          formulaText = Instruct(formulaInstruct);
        }
      }
      return formulaText;
    }
    // 
    switch (contentType) {
      case Cell.TYPE.STRING:
      case Cell.TYPE.NUMBER:
      case Cell.TYPE.DATE_TIME: {
        return text;
      }
      case Cell.TYPE.RICH_TEXT: {
        return richText;
      }
    }
    // 
    return SheetUtils.EMPTY;
  }

  /**
   * 
   * @param icons
   */
  setIcons(icons) {
    this.icons = icons;
  }

  /**
   * 
   * @param format
   */
  setFormat(format) {
    this.format = format;
    this.formatText = null;
    this.setContentWidth(0);
  }

  /**
   * 
   * @param text
   */
  setText(text) {
    this.text = text;
    this.richText = new RichFonts();
    this.formatText = null;
    this.formulaText = null;
    this.formula = null;
    this.formulaInstruct = null;
    this.setContentWidth(0);
  }

  /**
   * 
   */
  setRichText(text) {
    this.text = null;
    this.formatText = null;
    this.richText = text;
    this.formulaText = null;
    this.formula = null;
    this.formulaInstruct = null;
    this.setContentWidth(0);
  }

  /**
   * 
   */
  setFormula(formula) {
    this.text = null;
    this.richText = new RichFonts();
    this.formatText = null;
    this.formulaText = null;
    this.formula = formula;
    this.formulaInstruct = null;
    this.setContentWidth(0);
  }

  /**
   * 
   * @param ruler
   */
  setRuler(ruler) {
    this.ruler = ruler;
  }

  /**
   * 
   * @param type
   */
  setContentType(type) {
    this.contentType = type;
  }

  /**
   * 
   * @returns {*}
   */
  hasFormula() {
    return SheetUtils.isNotUnDef(this.formula);
  }

  /**
   * 
   * @returns {boolean}
   */
  isReadOnly() {
    return this.readOnly;
  }

  /**
   * 
   * @returns {boolean}
   */
  isEmpty() {
    return SheetUtils.isBlank(this.getComputeText());
  }

  /**
   * 
   * @param attr
   */
  setFontAttr(attr) {
    this.fontAttr = attr;
  }

  /**
   * 
   * @param attr
   */
  setBorderAttr(attr) {
    this.borderAttr = attr;
  }

  /**
   * 
   * @returns {Cell}
   */
  clone() {
    const { background, text, format, custom } = this;
    const { richText, fontAttr, borderAttr } = this;
    const { contentWidth, icons, contentType, formula } = this;
    return new Cell({
      background,
      format,
      text,
      formula,
      custom,
      richText,
      fontAttr,
      borderAttr,
      contentWidth,
      icons,
      contentType,
    });
  }

  /**
   * toJSON
   */
  toJSON() {
    const {
      text,
      icons,
      background,
      format,
      fontAttr,
      borderAttr,
    } = this;
    return {
      text,
      icons,
      background,
      format,
      fontAttr,
      borderAttr,
    };
  }

  /**
   * toString
   * @returns {string|*}
   */
  toString() {
    return this.getFormatText();
  }

  /**
   * 
   * @param width
   */
  setContentWidth(width) {
    this.contentWidth = width;
  }

  /**
   * 
   * @param height
   */
  setContentHeight(height) {
    this.contentHeight = height;
  }

}

/**
 * 
 */
Cell.TYPE = {
  // 
  NUMBER: 0,
  // 
  STRING: 1,
  // 
  RICH_TEXT: 2,
  // 
  DATE_TIME: 3,
};

export {
  Cell,
};
